import React, { useEffect, useState } from 'react';
import { Table, TableBody, TableCell, TableContainer, TableRow, Button, AppBar, Toolbar, Typography, IconButton, Menu, MenuItem, Box } from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';

function App() {
  const [users, setUser] = useState([]);
  const [anchorEl, setAnchorEl] = useState(null);

  useEffect(() => {
    fetch("https://api.gyanibooks.com/library/get_dummy_notes").then((result) => {
      result.json().then((resp) => {
        setUser(resp);
      });
    });
  }, []);

  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  return (
    <Box sx={{ backgroundColor: '#8594e4', minHeight: '100vh' }}>
      <AppBar position="static" sx={{ backgroundColor: '#430f58' }}>
        <Toolbar>
          <IconButton edge="start" color="inherit" aria-label="menu" onClick={handleMenuOpen}>
            <MenuIcon />
          </IconButton>
          <Menu
            anchorEl={anchorEl}
            open={Boolean(anchorEl)}
            onClose={handleMenuClose}
          >
            <MenuItem onClick={handleMenuClose}>ID</MenuItem>
            <MenuItem onClick={handleMenuClose}>USER</MenuItem>
            <MenuItem onClick={handleMenuClose}>Title</MenuItem>
            <MenuItem onClick={handleMenuClose}>Category</MenuItem>
          </Menu>
          <Typography variant="h6" style={{ flexGrow: 1 }}>
            Get API Call
          </Typography>
          <Button color="inherit">Login</Button>
        </Toolbar>
      </AppBar>
      <TableContainer>
        <Table border='10'>
          <TableBody border='1'>
            <TableRow>
              <TableCell sx={{ fontWeight: 'bold' }}>ID</TableCell>
              <TableCell sx={{ fontWeight: 'bold' }}>USER</TableCell>
              <TableCell sx={{ fontWeight: 'bold' }}>Title</TableCell>
              <TableCell sx={{ fontWeight: 'bold' }}>Category</TableCell>
              <TableCell sx={{ fontWeight: 'bold' }}>Notes</TableCell>
            </TableRow>
            {users.map((item, i) => (
              <TableRow key={i}>
                <TableCell>{item.id}</TableCell>
                <TableCell>{item.user}</TableCell>
                <TableCell>{item.title}</TableCell>
                <TableCell>{item.category}</TableCell>
                <TableCell>
                  {item.notes.split("\\n").map((note, index) => (
                    <div key={index}>{note}</div>
                  ))}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </Box>
  );
}

export default App;
